.modal-buy-now {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-contentt {
  width: 50%;
  height: 100%;
  background-color: #fefefe;
  border: 1px solid #888;
  margin: 15% auto;
  padding: 20px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #ff02ff;
  cursor: pointer;
  text-decoration: none;
}

.modal-contentt {
  background: var(--pink-bg);
