  position: absolute;
  overflow: hidden;
}

input[type="radio"] {
  display: none;
}

.card {
  width: 60%;
  height: 100%;
  cursor: pointer;
  margin: auto;
  transition: transform .4s;
  position: absolute;
  left: 0;
  right: 0;
}

.container-buy-now {
  width: 100%;
  max-height: 600px;
  height: 100%;
  transform-style: preserve-3d;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.cards {
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
  position: relative;
}

.cards label img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

#item-1:checked ~ .cards #song-3, #item-2:checked ~ .cards #song-1, #item-3:checked ~ .cards #song-2 {
  opacity: .4;
  z-index: 0;
  transform: translateX(-40%)scale(.8);
}

#item-1:checked ~ .cards #song-2, #item-2:checked ~ .cards #song-3, #item-3:checked ~ .cards #song-1 {
  opacity: .4;
  z-index: 0;
  transform: translateX(40%)scale(.8);
}

#item-1:checked ~ .cards #song-1, #item-2:checked ~ .cards #song-2, #item-3:checked ~ .cards #song-3 {
  opacity: 1;
  z-index: 1;
  transform: translateX(0)scale(1);
}

.upper-part {
  height: 36px;
  align-items: center;
  margin-bottom: 12px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.play-icon {
  margin-right: 10px;
}

.sub-line {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

.subtitle, .time {
  color: #c6c5c6;
  font-size: 12px;
  line-height: 16px;
}

.time {
  color: #a5a5a5;
  margin-left: auto;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
}

.progress-bar {
  height: 3px;
  width: 100%;
  background-color: #e9efff;
  border-radius: 2px;
  overflow: hidden;
}

.progress {
  width: 60%;
  height: 100%;
  background-color: #2992dc;
  border-radius: 6px;
  display: block;
  position: relative;
}

.info-area {
  width: 100%;
