.testimonials {
    display: flex;
    justify-content: center;

}

.carousel {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.review {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 80%;
    flex: 0 0 100%;
    transition: transform 0.6s ease;
    display: none;
}

.review.active {
    display: flex;
}

.rvw {
    display: flex;
    text-align: center;
    color: var(--black);
    background-color: var(--cloudy);
    font-size: var(--primary-font-size);
    font-weight: var(--weight-fnt-medium);
    line-height: 30px;
    position: relative;
    border: 1px solid var(--reviews-border);
    width: 280px;
    height: 327px;
    justify-content: center;
    text-align: start;
}

.rvw-text blockquote::before {
    content: url('../images/reviews/quotation-marks.png');
    width: 54px;
    height: 40px;
}

.rvw-text {
    position: relative;
    width: 180px;
    height: 213px;
    top: 20px;
}

.rvw::after {
    content: url('../images/reviews/red-ellipsis.png');
    position: absolute;
    right: 48.5%;
    top: 100%;
 }

.comment {
    position: relative;
    display: inline;
    background-image: repeating-linear-gradient(to top,
        rgba(216, 208, 208, 100%),
        rgba(250, 250, 250, 100%));
    background-size: 100% 2px;
    background-repeat: no-repeat;
    background-position: 0 100%;
    padding: 0px 20px;
}

.review p {
    padding-top: 16px;
    font-family: var(--primary-font-size);
    font-size: var(--reviews-names-font-size);
    font-weight: var(--weight-fnt-medium);
    top: 20px;
}

.review img {
    margin-top: 120px;
    margin-bottom: 16px;
}

.dots {
    margin-top: 32px;
    margin-bottom: 120px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 10px;

}

.dots a {
    text-decoration: none;
}

.dot {
    width: 12px;
    height: 12px;
    background: url('../images/reviews/grey-ellipse.png');
    text-decoration: none;
    border-radius: 50%;
    position: relative;
    margin-left: 10px;
    cursor: pointer;
    transition: background 0.6s ease;
}

.dot:visited,
.dot:hover,
.dot:active {
    background: url('../images/reviews/home.png');
}

.dot.active {
    background: url('../images/reviews/home.png');
}

/* Media queries for screens wider than 768px */
@media screen and (min-width: 768px) {
    .rvw {
        width: 620px;
        height: 164px;
        text-align: center;
    }
    .rvw-text {
        width: 608px;
        height: 108px;
    }
}

@media screen and (min-width: 1280px) {
 .rvw {
    width: 968px;
    height: 138px;
 }

 .rvw-text {
    width: 822px;
    height: 75px;
 }

 .carousel {
    width: 100%;
    margin-right: 0;
 }
}

